import { PrestamoConstants } from "../constants/prestamo.constants";

const initialState = {
    prestamo: {},
    pago: {},
    loading: false,
    lista_prestamos: []
}

export const PrestamoReducer = (state = initialState, action) => {
    switch (action.type) {
        case PrestamoConstants.Initial:
            return {
                ...state,
                lista_prestamos: [],
                prestamo: {},
                pago: {},
            }
        case PrestamoConstants.Create:
            return {
                ...state,
                prestamo: action.payload,
            }
        case PrestamoConstants.Read:
            return {
                ...state,
                prestamo: action.payload
            }
        case PrestamoConstants.Update:
            return {
                ...state,
                prestamo: action.payload
            }
        case PrestamoConstants.Delete:
            return {
                ...state,
                prestamo: {}
            }
        case PrestamoConstants.searchByClientId:
            return {
                ...state,
                lista_prestamos: action.payload
            }
        case PrestamoConstants.reCalculateInteres:
            return {
                ...state,
                prestamo: { ...state.prestamo, valor_interes: action.payload }
            }
        case PrestamoConstants.GeneratePago:
            const lista_prestamos = [...state.lista_prestamos];
            const index = lista_prestamos.findIndex(item => item._id === action.payload._id);
            lista_prestamos[index] = action.payload;
            return {
                ...state,
                prestamo: action.payload
            }
        case PrestamoConstants.deletePago:
            return {
                ...state,
                prestamo: action.payload
            }
        case PrestamoConstants.getPrestamosActivos:
            return {
                ...state,
                prestamos: action.payload
            }    

        case PrestamoConstants.startLoading:
            return {
                ...state,
                loading: true
            }
        case PrestamoConstants.finishLoading:
            return {
                ...state,
                loading: false,
            }

        default:
            return { ...state };
    }

}